<template>
  <div class="member">
    <h1>會員專區</h1>
    <div class="member__container">
      <div class="member__body">
        <ul class="member__list">
          <li class="member__item">
            <div class="mCollapse">
              <div class="mCollapse__header">
                <a href="#"
                  class="mCollapse__header"
                  :class="infoCollapse ? null : 'collapsed'"
                  @click.prevent="infoCollapse = !infoCollapse"
                >
                  會員資料
                </a>
              </div>
              <b-collapse v-model="infoCollapse">
                <div class="mCollapse__body">
                  <editable-text-block
                  :title="'姓名'"
                  :k="'name'"
                  :value="member.name"
                  :type="'text'"
                  @emit_newValue='change_member_info'
                  ></editable-text-block>
                  <editable-text-block
                  :title="'生日'"
                  :k="'birthday'"
                  :value="member.birthday"
                  :type="'date'"
                  @emit_newValue='change_member_info'
                  ></editable-text-block>
                  <editable-text-block
                  :title="'電話'"
                  :k="'tel'"
                  :value="member.tel"
                  :type="'text'"
                  @emit_newValue='change_member_info'
                  ></editable-text-block>
                  <editable-text-block
                  :title="'Email'"
                  :k="'email'"
                  :value="member.email"
                  :type="'email'"
                  @emit_newValue='change_member_info'
                  ></editable-text-block>
                  <editable-text-block
                  :title="'密碼'"
                  :k="'password'"
                  :value="member.password"
                  :type="'password'"
                  @emit_newValue='change_member_info'
                  ></editable-text-block>
                </div>
              </b-collapse>
            </div>
          </li>
          <li class="member__item">
            <div class="mCollapse">
              <div class="mCollapse__header">
                <a href="#"
                  class="mCollapse__header"
                  :class="orderCollapse ? null : 'collapsed'"
                  @click.prevent="orderCollapse = !orderCollapse"
                >訂單查詢</a>
              </div>
              <b-collapse v-model="orderCollapse">
                <div class="mCollapse__body">
                  <order-collapse
                  :member='member'
                  :orders='orders'
                  ></order-collapse>
                </div>
              </b-collapse>
            </div>
          </li>
          <li class="member__item">
            <div class="mCollapse">
              <div class="mCollapse__header">
                <a href="#"
                  class="mCollapse__header"
                  :class="favoriteCollapse ? null : 'collapsed'"
                  @click.prevent="favoriteCollapse = !favoriteCollapse">
                  喜好項目
                </a>
              </div>
              <b-collapse v-model="favoriteCollapse">
                <div class="mCollapse__body">
                  body
                </div>
              </b-collapse>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import EditableTextBlock from '../../components/EditableTextBlock.vue';
import OrderCollapse from '../../components/OrderCollapse.vue';

export default {
  components: { EditableTextBlock, OrderCollapse },
  computed: {
    member() {
      let member;
      this.$bus.$on('son_get_data', (data) => {
        member = data.member;
      });
      this.$bus.$emit('son_call_data');
      return member;
    },
  },
  data() {
    return {
      infoCollapse: true,
      orderCollapse: true,
      favoriteCollapse: true,
    };
  },
  methods: {
    change_member_info(key, value) {
      this.member[key] = value;
    },
  },
  created() {
    this.$bus.$emit('toggleCart', false);
  },
};
</script>
